@import "var";

.app-footer {
  position: fixed;
  width: 100%;
  bottom: 0;
}

.space_button {
  width: 100%;
  height: 48px;
  text-align: center;
  background: rgba(40, 40, 40, 0.8);
  border: dashed 1px white;
  color: white;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  &:hover {
    color: $theme-color;
  }

  &:active {
    background: rgba(40, 40, 40, 0.9);
    border: solid 1px white;
  }
}
